@charset "UTF-8";

.why_gnb_container{
    width:100%; height:80px; line-height:80px; background-color:#333; text-align:center; position:relative;
    strong{
        font-size:36px; color:$white;
        img{vertical-align:baseline; margin:0 0 0 8px;}
    }
    h1{width:80px; height:100%; background:url(../../images/layout/icon_whyoled.jpg) 50% no-repeat; position:absolute; left:30px; display:block}
    h1 a{width:100%; height:100%; display:block; }
    .oled_page{
        width:300px; height:38px; line-height:38px; color:$white; border:2px solid $white; font-size:16px; font-weight:$bold; display:block;
        position:absolute; top:21px; right:30px;
        @include borderRadius(80px);
        @include transition(all);
        &:hover{color:#000; background-color:$white;}
    }
}
.why_footer{
    width:100%; height:50px; background-color:$black; color:#ccc; text-align:center; position:relative;
    p{font-size:12px; line-height:50px;}
    ul{
        width:150px; position:absolute; top:50%; left:30px;
        @include translate(0, -50%);
        @extend %clearfix;
        li{float:left;}
        li:last-child a{border-left:1px solid #ccc;}
        a{font-size:12px; color:#ccc; padding:0 10px 0; display:block; }
    }
}
.why_oled_container{
    width:100%; min-width:1420px; overflow:hidden;
    // common
    video{vertical-align:top;}
    .why_video_wrap{position:relative; width:1600px; margin:0 auto;}
    .why_video_wrap img{width:100%;}
    .why_video_wrap .btn_wrap{position:absolute; top:0; left:0;}
    .why_video_wrap .disclaimer{font-size:14px; line-height:20px; font-weight:$regular; margin:16px 0 0;}
    .why_img_wrap{width:1600px;margin:0 auto; position:relative;}
    .why_img_wrap img{width:100%;}

    .txt_wrap{
        text-align:center; margin:0 0 80px;
        h2{font-size:70px; line-height:80px; font-weight:$bold; letter-spacing:-3px; color:$black;}
        p{font-size:22px; line-height:32px; font-weight:$regular; letter-spacing:1px; color:$gray2; margin:25px 0 0;}
    }

    // //common
    .get_better_wrap{
        background-color:#f5f5f5; padding:120px 0; text-align:center; color:#000;
        &>h2{
            font-size:90px; line-height:100px; letter-spacing:-2px; margin:0 0 150px;
            &:after{width:96px; height:4px; content:""; display:block; background-color:#000; margin:40px auto 0;}
        }
    }
    .best_resolution{
        margin:150px 0 0;
        .txt_wrap{position:relative; z-index:5; margin:0;}
        .why_video_wrap{position:relative; margin:70px auto 200px;}
        .why_video_wrap video{width:100%;}
    }
