@charset "UTF-8";
.gate_container{
    min-width:1420px; height:100vh; overflow:hidden; position:relative; background:url(../../images/studio/gate_img.jpg) 50% no-repeat; background-size:cover;
    h2{width:100%; text-align:center; position:absolute; top:10%; left:0;}
    .room_btn{
        display:inline-block; padding:15px 25px; border:1px solid $white; font-size:em(20); color:$white; font-weight:$bold;
        position:absolute; top:5%; right:2%;
        @include borderRadius(30px);
        @include transition(all);
        &:hover,
        &:active{background-color:$white; color:$black;}
    }
    ul{
        width:100%; display:flex; justify-content:center; flex-wrap:nowrap; position:absolute; bottom:15%; left:0;
        li{width:em(300); height:em(410); background-color:$white; margin:0; position:relative;}
        .oled_card{
            background:url(../../images/studio/gate_oled.jpg) 0 0 no-repeat; background-size:cover; margin:0 15px 0;
            @include perspectiveY(1000, 12, 1);
        }
        .qned_card{
            background:url(../../images/studio/gate_qned.jpg) 0 0 no-repeat; background-size:cover;
            @include perspectiveY(1000, 12, .94);
            img{margin:5px 5px 0 0;}
        }
        .virtual_card{
            background:url(../../images/studio/gate_virtual.jpg) 0 0 no-repeat; background-size:cover; background-color:$black; color:$white;
            @include scale(.92);
            .btn_wrap{
                strong{font-weight:$white;}
                //a{
                //    border:1px solid $white; color:$white;
                //    &:hover,
                //    &:active{background-color:$white; color:$black;}
                //}
                p{color:$white; font-weight:$block; margin:25px 0 0;}
            }
        }
        .nanocell_card{
            background:url(../../images/studio/gate_nanocell.jpg) 0 0 no-repeat; background-size:cover;
            @include perspectiveY(1000, -12, .94);
        }
        .av_card{
            background:url(../../images/studio/gate_av.jpg) 0 0 no-repeat; background-size:cover; margin:0 15px 0;
            @include perspectiveY(1000, -12, 1);
        }
        .btn_wrap{
            width:100%; text-align:center; position:absolute; left:0; bottom:em(56);
            strong{width:100%; display:block; font-size:em(36); font-weight:$block;}
            a {
                display:inline-block; padding:10px 20px; border:1px solid $black; font-size:em(14); color:$black; font-weight:$bold; margin:em(20) 0 0;
                @include borderRadius(20px);
                @include transition(all);
                &:hover,
                &:active{background-color:$black; color:$white;}
            }
        }
    }
}

.page_studio_container{
    min-width:1420px; overflow:hidden; position:relative;
    .studio_page{
        .swiper-slide{
            width:100% !important; height:100vh; overflow:hidden;
            & > img{
                width:100%; height:auto; position:absolute; top:calc(50% - 50px); left:50%;
                @include translate(-50%, -50%);
            }
        }
        .link_btn {
            position:absolute; z-index:15; text-align:center;
            strong { font-size:em(29); color:$black; font-weight:$bold; line-height:em(32); display:block; letter-spacing:em(-1); }
            a {
                font-size:em(11); font-weight:$medium; display:inline-block; color:$white; border:1px solid $black; padding:em(10) em(30); background-color:#242526; margin:5% 0 0;
                @include borderRadius(20px);
                @include transition(all, .5s);
                &:hover, &:active { color:$black; background-color:$white; }
            }
        }
        //oled_studio
        .oled_studio {
            .oled_why {
                width:em(220); height:em(363); top:-5%; right:14.5%; background:url("../../images/studio/why_oled_bg.png") 50% no-repeat;
                a { width:100%; height:100%; display:block; background-color:transparent; border:none; padding:0; }
            }
            .oled_r1, .oled_z1, .oled_g1, .oled_c1, .oled_b1 {
                width:25%;
                @include translate(0, -50%);
                .oled_txt {
                    @extend %clearfix;
                    strong { display:inline-block; padding:1% 5% 1% 0; border-right:1px solid #6f6e6f; font-size:em(27); }
                    span { display:inline-block; font-size:em(20); padding:0 0 0 3%; font-weight:$medium; position:relative; top:em(-3); color:$black; }
                }
            }
            .oled_b1 { top:53%; left:0%; }
            .oled_b1 .oled_txt span{padding:0;}
            .oled_g1 { top:40%; left:17.5%; }
            .oled_r1 { top:43%; left:37.5%; }
            .oled_z1 { top:40%; left:57%; }
            .oled_c1 { top:55.5%; left:77%; }
            .link_qr { z-index:15; }
            .oled_gallery_qr {
                position:absolute; top:30px; left:38px;
                img { width:155px; height:155px; }
                p { font-weight:$bold; font-size:15.5px; margin:10px 0 0;}
            }
        }
        //qned_studio
        .qned_studio {
            .qned_8k75, .qned_8k86, .qned_4k86, .qned_4k75 {
                width:25%;
                @include translate(0, -50%);
                .qned_txt {
                    @extend %clearfix;
                    strong { display:inline-block; padding:1% 5% 1% 0; border-right:1px solid #6f6e6f; font-size:em(27); }
                    span { display:inline-block; font-size:em(20); padding:0 0 0 3%; font-weight:$medium; position:relative; top:em(-3); color:$black; }
                }
            }
            .qned_why {
                width:em(220); height:em(363); top:-5%; right:14.5%; background:url("../../images/studio/why_qned_bg.png") 50% no-repeat;
                a { width:100%; height:100%; display:block; background-color:transparent; border:none; padding:0; margin:0; }
            }
            .qned_8k75 { top:47.5%; left:5%; }
            .qned_8k86 { top:38.5%; left:25%; }
            .qned_4k86 { top:42%; left:52%; }
            .qned_4k75 { top:49%; left:71.5%; }
        }
        //nanocell_studio
        .nanocell_studio {
            .nano099, .nano090, .nano085, .nano075 {
                width:25%;
                @include translate(0, -50%);
                .nano_txt {
                    @extend %clearfix;
                    strong { display:inline-block; padding:1% 5% 1% 0; border-right:1px solid #6f6e6f; font-size:em(27); }
                    span { display:inline-block; font-size:em(20); padding:0 0 0 3%; font-weight:$medium; position:relative; top:em(-3); color:$black; }
                }
            }
            .nano099 { top:42%; left:6%; }
            .nano090 { top:40%; left:30%;}
            .nano085 { top:41.5%; left:51.5%; }
            .nano075 { top:49%; left:70.5%;}
        }
        //av_studio
        .av_studio {
            .av_Gp9, .av_qp5, .av_Gsp11r, .av_fp8, .av_pl7 {
                @include translate(0, -50%);
                .av_txt {
                    @extend %clearfix;
                    strong {
                        float:left; border-right:1px solid #394754; padding:3% em(20) 2% 0;
                        img { width:100%; height:auto; }
                    }
                    span { display:inline-block; padding:2.2% 0 0 em(15); font-size:em(23); font-weight:$medium; position:relative; color:$black; left:em(3); }
                }
                a { margin:em(15) 0 0; }
            }
            .av_Gp9 {
                top:65%; left:7%;
                strong { width:em(153); height:em(27); }
            }
            .av_qp5 {
                top:51%; left:28%;
                strong { width:em(77); height:em(27); }
            }
            .av_Gsp11r {
                top:48%; left:52%;
                strong { width:em(132); height:em(25); }
            }
            .av_fp8 {
                top:78%; left:58%;
                strong { width:em(117); height:em(26); }
            }
            .av_pl7 {
                top:62%; left:77%;
                strong { width:em(137); height:em(26); }
            }
        }
        .studio_arrow_btn{
            width:70px; height:70px; border:1px solid $black; background-color:rgba(255,255,255,.3); margin:0;
            @include borderRadius(35px);
            @include transition(all);
            &:hover,
            &:active{background-color:rgba(255,255,255,.8)}
        }
        .swiper-button-next.studio_arrow_btn{right:50px; background-image:url(../../images/studio/icon_arrow.png); background-position:50%; background-repeat:no-repeat; background-size:auto;}
        .swiper-button-prev.studio_arrow_btn{
            left:50px; background-image:url(../../images/studio/icon_arrow.png); background-position:50%; background-repeat:no-repeat; background-size:auto;
            @include rotate(180);
        }
    }
}
.studio_footer{
    width:100%; height:50px; background-color:$black; color:#ccc; text-align:center; position:fixed; bottom:0; left:0; z-index:10;
    p{font-size:12px; line-height:50px;}
    ul{
        width:150px; position:absolute; top:50%; left:30px;
        @include translate(0, -50%);
        @extend %clearfix;
        li{float:left;}
        li:last-child a{border-left:1px solid #ccc;}
        a{font-size:12px; color:#ccc; padding:0 10px 0; display:block; }
    }
}