@charset "UTF-8";

.why_oled_container{
    width:100%; min-width:1420px; overflow:hidden;
    // common
    video{vertical-align:top;}
    .why_video_wrap{position:relative; width:1600px; margin:0 auto;}
    .why_video_wrap img{width:100%;}
    .why_video_wrap .btn_wrap{position:absolute; top:0; left:0;}
    .why_img_wrap{width:1600px;margin:0 auto; position:relative;}
    .why_img_wrap img{width:100%;}
    .play_btn{
        width:70px; height:70px; border:2px solid $white; position:absolute; bottom:50px; right:50px; z-index:10; overflow:hidden; text-indent:-9999px; display:none; opacity:0;
        @include borderRadius(50px);
        @include transition(all);
        &:after{
            width:0; height:0; content:""; display:block; border:13px solid transparent; border-left:20px solid $white ; position:absolute; top:20px; left:28px;
            @include transition(all);
        }
        &:hover{
            background-color:$white;
            &:after{ border-left-color:$black;}
        }
    }
    .why_video_wrap .disclaimer{font-size:14px; line-height:20px; font-weight:$regular; margin:16px 0 0;}
    .why_img_wrap .disclaimer{width:1000px; font-size:14px; line-height:20px; font-weight:$regular; margin:16px auto 0;}
    .txt_wrap{
        text-align:center; margin:0 0 80px;
        h2{font-size:70px; line-height:80px; font-weight:$bold; letter-spacing:-3px; color:$black;}
        p{font-size:22px; line-height:32px; font-weight:$regular; letter-spacing:1px; color:$gray2; margin:25px 0 0;}
    }
    .swipe_wrap{position:relative;}
    .swipe_indicator_wrap{width:100%; min-width:1420px; max-width:1600px; height:60px; margin:10px auto 0; position:relative;}
    .swipe_next_btn,
    .swipe_prev_btn{
        width:50px; height:50px; position:absolute; top:calc(50% - 25px); border: 1px solid $black; margin:0;
        @include borderRadius(25px);
    }
    .swipe_next_btn{
        right:80px; background:url(../../images/whyoled/icon_arrow.jpg) 50% no-repeat; background-color:$white;
        @include rotate(180) ;
    }
    .swipe_prev_btn{left:80px; background:url(../../images/whyoled/icon_arrow.jpg) 50% no-repeat; background-color:$white;}
    .custom_pagination{
        top:50%; left:50%;
        @include translate(-50%, -50%);
        .swiper-pagination-bullet{
            margin:0 5px 0; border:1px solid $black; background-color:$white;
            @include opacity(1);
        }
        .swiper-pagination-bullet-active{background-color:$black;}
    }
    // //common

    .seen_oled{
        margin:100px 0 0;
        .txt_wrap{position:relative; z-index:5; margin:0;}
        .why_video_wrap{position:relative; margin:70px auto 200px;}
        .why_video_wrap video{width:100%;}
        .why_video_wrap .disclaimer { font-size: 14px; }
    }
    .makes_oled{
        margin:0; position:relative; overflow:hidden;
        .makes_oled_seen{
            position:relative; overflow:hidden; text-align:center; margin:1% 0 0 ;
            .makes_img02{
                position:absolute; top:0; left:0; display:none;
                @include opacity(0);
            }
            .center_line{
                width:4px; height:100%; position:absolute; top:0; left:50%; background-color:$white; display:none;
                @include translate(-50%, 0);
                @include opacity(0);
            }
            .tv_display{
                text-align:center; display:none;
                @include opacity(0);
                strong{width:50%; font-size:80px; color:$white; letter-spacing:2px; line-height:70px; position:absolute; bottom:100px; display:inline-block; text-shadow:5px 4px 15px #000;}
                .display_led{left:0}
                .display_oled{right:0}
            }
            .makes_txt_wrap{
                width:100%; position:absolute; top:50%; left:50%;
                @include translate(-50%, -50%);
                strong{
                    width:100%; font-size:80px; color:$white; line-height:100px; position:absolute; top:50%; left:0; display:block; text-shadow:2px 3px 10px #000;
                }
                .last_txt{
                    width:100%; position:absolute; top:50%; left:0;
                    strong{position:relative;}
                }
                .makes_show{
                    width:260px; height:70px; line-height:70px; background-color:$white; font-size:24px; font-weight:$bold; text-align:center; color:#505050; margin:40px 0 0; position:relative;
                    @include borderRadius(40px);
                    &:after{
                        width:280px; height:90px; display:block; content:""; background-color:transparent; border:1px solid $white;
                        position:absolute; top:-10px; left:-10px;
                        @include borderRadius(45px);
                    }
                }
                .makes_txt01,
                .makes_txt02{
                    display:none;
                    @include opacity(0);
                    @include translate(0, -40%);
                }
                .makes_txt03{
                    display:none;
                    @include opacity(0);
                    @include translate(0, -50%);
                }
            }

        }
        .makes_video{
            position:absolute; top:0; left:0; z-index:10; display:none;
            @include opacity(0);
            strong{
                width:100%; font-size:80px; color:$white; line-height:100px; position:absolute; top:50%; left:0; display:none; text-shadow:2px 3px 10px #000;
                @include translate(0, -50%);
                @include opacity(0);
            }
        }
        .makes_video .video{width:100%; height:100%;}

        .init_makes_video{
            position:absolute; top:0; left:0; z-index:10; display:none;
            @include opacity(0);
        }
        .init_makes_video .video{width:100%; height:100%;}

    }
    .this_shade{
        margin:200px 0 0; position:relative; overflow:hidden;
        .monitor_seen_wrap{
            padding:1% 0 0;
            .monitor_seen{width:100%; height:100%; position:absolute; margin:0; z-index:10;}
            .monitor_img01{width:100%;}
            .monitor_txt01{
                width:100%; position:absolute; top:50%; left:0; text-align:center; z-index:10; display:none;
                @include opacity(0);
                @include translate(0, -40%);
                strong{width:100%; font-size:80px; color:$white; line-height:100px;  display:block; text-shadow:2px 3px 10px #000; z-index:10; }
                .monitor_show{
                    width:360px; height:70px; line-height:70px; background-color:$white; font-size:24px; font-weight:$bold; text-align:center; color:#505050; margin:40px 0 0; position:relative;
                    @include borderRadius(40px);
                    &:after{
                        width:380px; height:90px; display:block; content:""; background-color:transparent; border:1px solid $white;
                        position:absolute; top:-10px; left:-10px;
                        @include borderRadius(45px);
                    }
                }
            }
        }

        .monitor_compare_wrap { max-width:1600px; margin:0 auto 0; }

        .monitor_compare_wrap p.cation { color:#bbb; font-size:16px; padding:20px 0 0; }

        .monitor_compare_wrap .monitor_compare .compare_box { background-image:url(../../images/whyoled/shade_oled01.jpg); }

        .monitor_compare_wrap .monitor_compare .lcd_screen .img_wrap { background-image:url(../../images/whyoled/shade_led01.jpg); }

        .monitor_compare .compare_wrap { height:0; padding:0 0 56.2%; position:relative; color:#fff; -ms-background-position-x:0; background-position-x:0; -ms-background-position-y:0; background-position-y:0; background-repeat:no-repeat; }

        .monitor_compare .compare_box { width:100%; height:100%; position:absolute; top:0; left:0; z-index:1; overflow:hidden; -ms-background-position-x:0; background-position-x:0; -ms-background-position-y:50%; background-position-y:50%; background-repeat:no-repeat; background-size:auto 100%;}

        .monitor_compare .lcd_screen { width:calc(50% + 64px); height:100%; position:relative; overflow:hidden; }

        .monitor_compare .lcd_screen:after { width:1px; height:100%; display:block; content:""; position:absolute; right:64px; top:0; background:url(../../images/whyoled/compare_line.png) 0 0 repeat-y; }

        .monitor_compare .lcd_screen .img_wrap { width:calc(100% - 64px); height:100%; overflow:hidden; position:relative; -ms-background-position-x:0; background-position-x:0; -ms-background-position-y:50%; background-position-y:50%; background-repeat:no-repeat; background-size:auto 100%;}

        .monitor_compare .oled_txt, .monitor_compare .lcd_txt { position:absolute; top:30px; font-size:34px; }

        .monitor_compare .oled_txt strong, .monitor_compare .lcd_txt strong { display:block; white-space:nowrap; font-size:60px; font-weight:$medium;}

        .monitor_compare .oled_txt { right:40px; }

        .monitor_compare .lcd_txt { left:40px; }

        .monitor_compare .moving_btn { width:62px; height:62px; background-color:#fff; position:absolute; right:33px; top:50%; margin:-31px 0 0; z-index:2; -webkit-border-radius:100%; -moz-border-radius:100%; border-radius:100%; -webkit-transition:background-color 0.3s ease; -moz-transition:background-color 0.3s ease; -o-transition:background-color 0.3s ease; -ms-transition:background-color 0.3s ease; }

        .monitor_compare .moving_btn:hover { background-color:#000; }

        .monitor_compare .moving_btn:hover:after { border-color:#1e1e1e; }

        .monitor_compare .moving_btn:hover .btn_dot { background-color:#fff; }

        .monitor_compare .moving_btn:hover .btn_dot:before, .monitor_compare .moving_btn:hover .btn_dot:after { background-color:#fff; }

        .monitor_compare .moving_btn:after { width:80px; height:80px; display:block; content:""; position:absolute; top:-9px; left:-9px; -webkit-border-radius:100%; -moz-border-radius:100%; border-radius:100%; border-width:3px; border-style:solid; border-color:#fff; -webkit-box-sizing:border-box; -moz-box-sizing:border-box; box-sizing:border-box; -webkit-transition:border-color 0.3s ease; -moz-transition:border-color 0.3s ease; -o-transition:border-color 0.3s ease; -ms-transition:border-color 0.3s ease; }

        .monitor_compare .moving_btn .btn_dot { width:4px; height:16px; display:block; position:absolute; top:50%; left:50%; background-color:#1e1e1e; -webkit-transform:translate(-50%, -50%); -moz-transform:translate(-50%, -50%); -ms-transform:translate(-50%, -50%); -o-transform:translate(-50%, -50%); transform:translate(-50%, -50%); -webkit-transition:all 0.2s ease; -moz-transition:all 0.2s ease; -o-transition:all 0.2s ease; -ms-transition:all 0.2s ease; text-indent:-9999px;}

        .monitor_compare .moving_btn .btn_dot:before, .monitor_compare .moving_btn .btn_dot:after { width:4px; height:100%; display:block; content:""; position:absolute; top:0; background-color:#1e1e1e; -webkit-transition:background-color 0.3s ease; -moz-transition:background-color 0.3s ease; -o-transition:background-color 0.3s ease; -ms-transition:background-color 0.3s ease; }

        .monitor_compare .moving_btn .btn_dot:before { left:8px; }

        .monitor_compare .moving_btn .btn_dot:after { left:-8px; }

        .monitor_compare .moving_btn .btn_arrow { width:122px; position:absolute; top:50%; left:50%; margin:-6px 0 0 -61px; }

        .monitor_compare .moving_btn .btn_arrow:before, .monitor_compare .moving_btn .btn_arrow:after { width:11px; height:11px; display:block; content:""; border-left:2px solid #fff; border-bottom:2px solid #fff; position:absolute; top:0; }

        .monitor_compare .moving_btn .btn_arrow:before { left:0; -webkit-transform:rotate(45deg); -moz-transform:rotate(45deg); -ms-transform:rotate(45deg); -o-transform:rotate(45deg); transform:rotate(45deg); }

        .monitor_compare .moving_btn .btn_arrow:after { right:0; -webkit-transform:rotate(225deg); -moz-transform:rotate(225deg); -ms-transform:rotate(225deg); -o-transform:rotate(225deg); transform:rotate(225deg); }

    }
    .flying_color{
        margin:200px 0 0; position:relative; overflow:hidden;
        .flying_swipe_wrap{position:relative;}
        .flying_swipe{
            .swiper-slide{
                //width:1420px !important;
                @include transition(all);
                @include opacity(.3);
                img{width:100%; height:auto;}
            }
            .swiper-slide-active{
                @include opacity(1);
            }
        }
        .swipe_indicator_wrap{width:1420px;}
        .swipe_next_btn{right:130px;}
        .swipe_prev_btn{left:130px;}
    }
    .color_fidelity{
        margin:100px 0 0; position:relative; overflow:hidden;
        .txt_wrap{
            strong{font-size:50px; line-height:50px; display:block; padding:0 0 20px;}
            p{
                font-size:20px;
            }
            .disclaimer{font-size:14px;}
        }
    }
    .new_light{
        margin:150px 0 0; position:relative; overflow:hidden; background-color:#f8f8f8; padding:0 0 100px;
        .txt_wrap{  margin:0; padding:100px 0 80px;}
        .light_swipe_wrap{position:relative; overflow:hidden;}
        .light_swipe{
            .swiper-slide{
                width:1600px; position:relative;
                img{width:100%; height:auto;}
            }
        }
        .light_dimm{width:100%; height:100%; background-color:rgba(0,0,0,.4); position:absolute; top:0; left:0;}
        .light_txt{
            width:100%; position:absolute; top:45%; left:50%; z-index:1;
            @include translate(-50%, -50%);
            strong{display:block; font-size:80px; line-height:100px; color:$white;}
            .light_btn{
                width:360px; height:70px; line-height:70px; background-color:$white; font-size:24px; font-weight:$bold; text-align:center; color:#505050; margin:50px 0 0; position:relative;
                @include borderRadius(40px);
                &:after{
                    width:380px; height:90px; display:block; content:""; background-color:transparent; border:1px solid $white;
                    position:absolute; top:-10px; left:-10px;
                    @include borderRadius(45px);
                }
            }
        }


        //loading
        .loading_var{
            width:100%; position:absolute; top:65%; left:0; z-index:10; display:none;
            @include opacity(0);
            @include translate(0, -50%);
            &>span{font-size:16px; color:$white; font-weight:$medium; letter-spacing:.5px; text-shadow:5px 3px 10px $black;}
        }

        .Loading-progress{
            height: 12px;
            width: 300px;
            margin:20px auto 0;
            border-radius: 3px;
            box-shadow:5px 3px 10px $black;
            background: linear-gradient(90deg, red 0%, yellow 15%, lime 30%, cyan 50%, blue 65%, magenta 80%, red 100%);
            background-size: 200%;
            animation: moveGradient 5s linear infinite;
        }

        @keyframes moveGradient{
            0%{
                background-position: 0% 0%
            }
            100%{
                background-position: -200% 0%
            }
        }








    }
    .literally{
        margin:200px 0 0; position:relative; overflow:hidden;
        .video_box{ height:900px;}
        iframe{width:100%; height:100%;}
    }
    .the_eyes{
        margin:200px 0 0; position:relative; overflow:hidden;
    }
    .the_environment{
        margin:200px 0 100px; position:relative; overflow:hidden;
    }

}
